import { HttpClient } from "@angular/common/http";
import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { TranslateLoader, TranslateModule } from "@ngx-translate/core";
import { HttpLoaderFactory } from "./app.module";

// layouts
import { AdminComponent } from "./layouts/admin/admin.component";
import { AuthComponent } from "./layouts/auth/auth.component";
import { AdminLoginComponent } from "./views/auth/admin-login/admin-login.component";

// admin views
import { DashboardComponent } from "./views/admin/dashboard/dashboard.component";
import { MapsComponent } from "./views/admin/maps/maps.component";
import { SettingsComponent } from "./views/admin/settings/settings.component";
import { TablesComponent } from "./views/admin/tables/tables.component";
import { EditFormComponent } from "./views/auth/edit-form/edit-form.component";

// auth views
import { LoginComponent } from "./views/auth/login/login.component";
import { RegisterComponent } from "./views/auth/register/register.component";
import { SavedDataComponent } from "./views/auth/saved-data/saved-data.component";
import { UserCodeComponent } from "./views/auth/user-code/user-code.component";
import { ViewDataComponent } from "./views/auth/view-data/view-data.component";

// no layouts views
import { IndexComponent } from "./views/index/index.component";
import { LandingComponent } from "./views/landing/landing.component";
import { ProfileComponent } from "./views/profile/profile.component";
import { ViewApplicantComponent } from "./views/admin/view-applicant/view-applicant.component";
import { EditApplicantComponent } from "./views/admin/edit-applicant/edit-applicant.component";
import { Applicant } from "./models/applicant";



const routes: Routes = [
  // admin views
  {
    path: "admin",
    component: AdminComponent,
    children: [
      { path: "settings", component: SettingsComponent },
      { path: "main", component: TablesComponent },
      { path: "view-applicant", component: ViewApplicantComponent },
      { path: "edit-applicant", component: EditApplicantComponent },
      { path: "", redirectTo: "main", pathMatch: "full" },
    ],
  },
  // auth views
  {
    path: "auth",
    component: AuthComponent,
    children: [
      { path: "login", component: LoginComponent },
      { path: "register", component: RegisterComponent },
      { path: "view-data", component: ViewDataComponent, data: Applicant },
      { path: "user-code", component: UserCodeComponent },
      { path: "edit-form", component: EditFormComponent },
      { path: "saved-data", component: SavedDataComponent },
      { path: "admin-login", component: AdminLoginComponent },

      { path: "", redirectTo: "register", pathMatch: "full" },
    ],
  },
  { path: "admin", component: AdminComponent },
  // no layout views
  { path: "saved-data", component: SavedDataComponent },
  { path: "admin-login", component: AdminLoginComponent },
  { path: "", component: IndexComponent },
  { path: "**", redirectTo: "", pathMatch: "full" },
];

@NgModule({
  imports: [RouterModule.forRoot(routes), TranslateModule
  ],
  exports: [RouterModule],
})
export class AppRoutingModule {}
