import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import { Applicant } from '../models/applicant';


@Injectable({
  providedIn: 'root'
})
export class ApplicantServiceService {
  myAppUrl: string;
  myApiUrl: string;
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json; charset=utf-8'
    })
  };
  constructor(private _http: HttpClient) {
      this.myAppUrl = 'http://127.0.0.1:8000/';
      this.myApiUrl = 'api/Applicants/';
  }
  
    getApplicant(): Observable<Applicant[]> {  
      return this._http.get<Applicant[]>(this.myAppUrl + this.myApiUrl)
      .pipe(
        retry(1)
      );
    } 
    
    addApplicant(Applicant): Observable<Applicant>{
      return this._http.post<Applicant>(this.myAppUrl + this.myApiUrl, JSON.stringify(Applicant), this.httpOptions)
      .pipe(
        retry(1)
      ) 
    }

    getApplicantId(ApplicantId: number): Observable<Applicant>{
      return this._http.get<Applicant>(this.myAppUrl + this.myApiUrl + ApplicantId)
      .pipe(
        retry(1)
      )
    }


    editApplicant(ApplicantId: number, Applicant): Observable<Applicant>{
      return this._http.put<Applicant>(this.myAppUrl + this.myApiUrl + ApplicantId, JSON.stringify(Applicant), this.httpOptions)
      .pipe(
        retry(1)
      )
    }

    deleteApplicant(ApplicantId: number): Observable<Applicant>{
      return this._http.delete<Applicant>(this.myAppUrl + this.myApiUrl + ApplicantId)
      .pipe(
        retry(1)
      )
    }


  
}
