import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import jsPDF from 'jspdf';
import html2canvas from 'html2canvas';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import domtoimage from 'dom-to-image';


@Component({
  selector: 'app-saved-data',
  templateUrl: './saved-data.component.html',
  styleUrls: ['./saved-data.component.css'],

 
})



export class SavedDataComponent implements OnInit {
  showModal = false;
  toggleModal(){
    this.showModal = !this.showModal;
  }

  

  printComponent(cmpName) {
    let printContents = document.getElementById(cmpName).innerHTML;
    let originalContents = document.body.innerHTML;

    document.body.innerHTML = printContents;

    window.print();

    document.body.innerHTML = originalContents;
}

  @ViewChild('register', {static: false}) register: ElementRef;


  public downloadAsPDF() {
    var node = document.getElementById('register');

    var img;
    var filename;
    var newImage;


    domtoimage.toPng(node, { bgcolor: '#fff' })

      .then(function(dataUrl) {

        img = new Image();
        img.src = dataUrl;
        newImage = img.src;

        img.onload = function(){

        var pdfWidth = img.width;
        var pdfHeight = img.height;

          // FileSaver.saveAs(dataUrl, 'my-pdfimage.png'); // Save as Image

          var doc;

          if(pdfWidth > pdfHeight)
          {
            doc = new jsPDF('l', 'px', [pdfWidth , pdfHeight]);
          }
          else
          {
            doc = new jsPDF('p', 'px', [pdfWidth , pdfHeight]);
          }


          var width = doc.internal.pageSize.getWidth();
          var height = doc.internal.pageSize.getHeight();


          doc.addImage(newImage, 'PNG',  10, 10, width, height);
          filename = 'mypdf_' + '.pdf';
          doc.save(filename);

        };


      })
      .catch(function(error) {

       // Error Handling

      });

  }

  delete() {
    this.http.delete('http://45.56.117.204:8000/api/Applicants/'+this.applicant.id+"/", {
      headers: {'Authorization':'Token 870d88a50b2847f998bcfb40bc072ff0e0af0ddf'}
    }).subscribe((data) => {
          console.log(data)
          this.router.navigateByUrl('');     
        });
  }

  constructor(private http: HttpClient, private router: Router) { }

  code: any
  applicant: any
  ngOnInit(): void {
    let data = history.state
    this.code = data.code
    this.applicant = data.data
    console.log(this.applicant.id)
    this.applicant.nationalImg = "http://45.56.117.204:8000"+this.applicant.nationalImg
    this.applicant.passportImg = "http://45.56.117.204:8000"+this.applicant.passportImg
  }


  
}
