import { HttpClient } from "@angular/common/http";
import { Component, OnInit, Input } from "@angular/core";
import { Router } from "@angular/router";
import { TranslateConfigService } from "src/app/services/translate-config.service";

@Component({
  selector: "app-card-table",
  templateUrl: "./card-table.component.html",
})
export class CardTableComponent implements OnInit {
  @Input()
  get color(): string {
    return this._color;
  }
  set color(color: string) {
    this._color = color !== "light" && color !== "dark" ? "light" : color;
  }
  private _color = "light";

  constructor(private translateConfigService: TranslateConfigService, private http: HttpClient, private router: Router) {}
  searchText
  error
  applicants
  auth

  navigateApplicant(applicant){
    this.router.navigateByUrl('/admin/view-applicant', { state: applicant });
  }

  
  ngOnInit(): void {
    this.http.get('http://45.56.117.204:8000/api/Applicants/', {
      headers: {'Authorization':'Token 870d88a50b2847f998bcfb40bc072ff0e0af0ddf'}
    }).subscribe((data) => {
      this.applicants = data
    }, (err: Response | any) => {
      if(err.status == 0) {this.error = "Connection Error"}
      this.error = this.translateConfigService.error("login")
    }
    );
    this.http.get('http://45.56.117.204:8000/api/auth/', {
      headers: {'Authorization':'Token 870d88a50b2847f998bcfb40bc072ff0e0af0ddf'}
    }).subscribe((data) => {
      this.auth = data
    }, (err: Response | any) => {
      if(err.status == 0) {this.error = "Connection Error"}
      this.error = this.translateConfigService.error("login")
    }
    );
  }
}
